code, kbd, pre, samp {
    font-family: Menlo, Consolas, "Courier New", monospace
}

code, samp {
    padding: 2px 4px;
    font-size: 90%;
    background-color: #F8F8F8;
    border-radius: 4px
}

kbd {
    padding: 2px 4px;
    font-size: 90%;
    color: #fff;
    background-color: #333;
    border-radius: 3px;
    -webkit-box-shadow: inset 0 -1px 0 rgba(0, 0, 0, .25);
            box-shadow: inset 0 -1px 0 rgba(0, 0, 0, .25)
}

kbd kbd {
    padding: 0;
    font-size: 100%;
    font-weight: bold;
    -webkit-box-shadow: none;
            box-shadow: none
}

pre {
    display: block;
    padding: 9.5px;
    margin: 0 0 10px;
    font-size: 15px;
    line-height: 1.5;
    word-break: break-all;
    word-wrap: break-word;
    color: #333;
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 4px
}

pre code {
    display: block;
    padding: 0;
    font-size: inherit;
    color: inherit;
    white-space: pre-wrap;
    background-color: transparent;
    border-radius: 0
}

.pre-scrollable {
    max-height: 340px;
    overflow-y: scroll
}

.pre-unstyled {
    background-color: transparent;
    border: none;
    border-radius: 0;
    margin: 0;
    padding: 0;
    color: inherit
}

pre.pre-scrollable {
    overflow-y: scroll
}

.inline-dark {
    background-color: #243641;
    border-radius: 3px;
    color: #B38F1B
}

.terminal {
    margin: 2px;
    height: 100%;
    background-color: #d4d9d9;
    padding: 6px 10px 12px 10px;
    border-radius: 6px;
    -webkit-box-shadow: 0 2px 0 rgba(0, 0, 0, .3);
            box-shadow: 0 2px 0 rgba(0, 0, 0, .3)
}

.terminal .terminal-dots {
    margin-bottom: 0;
    margin-top: 0;
    padding: 0
}

.terminal .terminal-dots li {
    border-radius: 6px;
    width: 12px;
    height: 12px;
    display: inline-block;
    margin-right: 15px
}

.terminal .terminal-dots li:nth-child(1) {
    background-color: #ED6B62;
}

.terminal .terminal-dots li:nth-child(2) {
    background-color: #F6BE4F;
}

.terminal .terminal-dots li:nth-child(3) {
    background-color: #66CD58;
}

.terminal pre[class*="language-"] {
    margin: 2px 0 0 0;
    border-radius: 3px;
    overflow-y: auto;
    height: 84%;
}



